<template lang="html">
	<div class="motorcade">
		<div class="headers-wrapper" style="border-bottom: 1px solid #e6e9f2;">
	      	<headers></headers>
	    </div>
	    <div class="motorcade_con">
		  	<ul class="mycar_l">
		    	<router-link to='/motorcade/mycar' tag="li" active-class="mycar_show">我的车队<i class="el-icon-arrow-right"></i></router-link>
		    	<router-link to="/motorcade/cardsearch" tag="li" active-class="mycar_show">证件查询<i class="el-icon-arrow-right"></i></router-link>
		    	<router-link to="/motorcade/sharegl" tag="li" active-class="mycar_show">共享管理<i class="el-icon-arrow-right"></i></router-link>
		  	</ul>
		  	<div class="mycar_r">
		  		<router-view></router-view>
		  	</div>
	    </div>
	</div>
</template>

<script>
import headers from "@/components/header"
export default {
  components:{
    headers
  }
}
</script>

<style lang="less">
.motorcade_con{
	width:1200px;
	margin: 0 auto;
	&:after{
		display: block;
		clear:both;
		content: "";
	}
}
.mycar_l{
	width: 200px;
	float: left;
	height: 100%;
	box-sizing: border-box;
}
.mycar_l li{
	box-sizing: border-box;
	padding: 20px 50px;
	cursor: pointer;
	font-size: 15px;
	color: rgb(110,110,110);
	border-bottom:1px solid rgb(240,240,240);
	i{
		float:right;
		font-size:12px;
		margin-top: 5px;
		color:#ddd;
	}
}
.mycar_r{
	float:right;
	width:1000px;
	box-sizing:border-box;
	background: rgb(250,251,255);
	border-left: 1px solid rgb(240,240,240);
}
.mycar_l li.mycar_show{
	color:#2e51a5;
	font-weight: bolder;
	background:rgb(245,246,251);
}
</style>



